import React from "react";
import {
    CardContent, List, ListItem,
    ToggleButton,
    ToggleButtonGroup,
    Typography
} from "@mui/material";

export const Direction = () => {
    const [transportation, setTransportation] = React.useState('public');

    const handleChange = (
        event: React.MouseEvent<HTMLElement>,
        newTransportation: string,
    ) => {
        setTransportation(newTransportation);
    };

    let info: string[]
    switch (transportation) {
        default:
        case "public":
            info = [
                "2 (4 )    2  , ''  ', '  ",
                "2 (3 )  5511 , ', '  ",
            ]
            break
        case "car":
            info = [
                "           400m ",
                "     ( )   400m ",
            ]
            break
        case "taxi":
            info = [
                ",   ' '  ' ' ",
                " 5~10 ",
            ]
            break
    }

    return (
        <CardContent>
            <Typography variant="h6"> </Typography>
            <Typography> 1  65( 56-1)</Typography>
            <Typography variant="subtitle2" color="gray" gutterBottom>   ''  !</Typography>
            <ToggleButtonGroup
                color="primary"
                value={transportation}
                exclusive
                onChange={handleChange}
            >
                <ToggleButton value="public"> </ToggleButton>
                <ToggleButton value="car"> </ToggleButton>
                <ToggleButton value="taxi"> </ToggleButton>
            </ToggleButtonGroup>
            <List dense>{info.map((v, id) => <ListItem key={id}><Typography variant="body2">- {v}</Typography></ListItem>)}</List>
        </CardContent>
    )
}