import {Component} from "react";
import FavoriteIcon from '@mui/icons-material/Favorite';
import {
    Timeline,
    TimelineConnector,
    TimelineContent,
    TimelineDot,
    TimelineItem,
    TimelineOppositeContent,
    TimelineSeparator
} from "@mui/lab";
import {Typography} from "@mui/material";

interface EventItem {
    date: string
    title: string
    description: string
    icon?: JSX.Element
}

const Event = (idx: number, event: EventItem) => {
    return (
        <TimelineItem key={idx}>
            <TimelineOppositeContent
                sx={{ m: 'auto 0' }}
                variant="body2"
                color="text.secondary"
                align={idx % 2 === 0 ? "left" : "right"}
            >
                {event.date}
            </TimelineOppositeContent>
            <TimelineSeparator>
                <TimelineConnector/>
                <TimelineDot variant="outlined">
                    {event.icon}
                </TimelineDot>
                <TimelineConnector/>
            </TimelineSeparator>
            <TimelineContent>
                <Typography component="span">{event.title}</Typography>
                <Typography variant="body2" color="text.secondary">{event.description}</Typography>
            </TimelineContent>
        </TimelineItem>
    )
}

const histories: EventItem[] = [
    {date: "2011. 07. 19", title: " ", description: "   "},
    {date: "2011. 08. 14", title: " ", description: " !", icon: <FavoriteIcon sx={{ color: "red" }}/>},
    // {date: "2013. 10. 30", title: "  ", description: "", icon: <SchoolIcon/>},
    {date: "2014. 04. 24", title: " ", description: " "},
    // {date: "2016. 01. 23", title: " ", description: ""},
    // {date: "2018. 09. 01", title: "  ", description: "2  "},
    {date: "2021. 02. 18", title: "  ", description: "  ver.2"},
    {date: "2022. 08. 20", title: "", description: "  ", icon: <Typography></Typography>},
]

export class History extends Component<any, any> {
    render() {
        return (
            <Timeline position="alternate">
                {histories.map((event: EventItem, idx: number) => Event(idx, event))}
            </Timeline>
        )
    }
}